import { ref } from 'vue';
import { getCurrentInstance } from "vue";
import { Toast } from 'vant';
import { areaList } from '@vant/area-data';
export default {
  setup() {
    let {
      proxy
    } = getCurrentInstance();
    const checked = ref(true);

    const fn = () => {
      proxy.$router.back();
    };

    const arrArea = [];
    const valueArea = "";
    const name = ref();
    const tel = ref();
    const address = ref();
    const show = ref(false);

    const onCancel = () => {
      proxy.show = false;
    };

    const onShow = () => {
      proxy.show = true;
    };

    const onConfirm = val => {
      proxy.show = false;
      proxy.arrArea = val;
      var addrInfo = val[0].name + "-" + val[1].name + "-" + val[2].name;
      proxy.valueArea = addrInfo;
    };

    const link = () => {
      let obj = {
        name: proxy.name,
        tel: proxy.tel,
        address: proxy.valueArea + " " + proxy.address,
        isDefault: proxy.checked
      };
      console.log(proxy.name, proxy.tel, obj.address);

      if (proxy.name != undefined && proxy.tel != undefined && obj.address != 'undefined') {
        if (localStorage.getItem("list")) {
          let list = JSON.parse(localStorage.getItem("list"));
          obj.id = list.length + 1;

          if (obj.isDefault == true) {
            list.forEach(val => {
              val.isDefault = false;
            });
          }

          list.push(obj);
          localStorage.setItem("list", JSON.stringify(list));
        } else {
          let list = [];
          obj.id = 1;
          list.push(obj);
          localStorage.setItem("list", JSON.stringify(list));
        }

        proxy.$router.push({
          path: "/addresslist"
        });
      } else {
        Toast("");
        console.log(obj, 1);
      }
    };

    return {
      fn,
      tel,
      show,
      link,
      name,
      onShow,
      checked,
      arrArea,
      address,
      areaList,
      onCancel,
      onConfirm,
      valueArea
    };
  },

  created() {
    this.checked = false;
  }

};